    export default {
    	data () {
    		return {
                title:'',
                cont:'',
                add:'',
                volume:'',
				aa:''
    		}
    	},
        onLoad(option) {
            console.log(option)
            this.title = option.title
            this.cont = option.cont;
			this.aa=this.cont.replace(/_/g,"=").replace(/\<img/gi, '<img style="max-width:100%;height:auto" ');
			console.log(this.aa)
